import { Popover, Space, Tag } from "@douyinfe/semi-ui";
import { useMemo } from "react";

/**
 * 
 * @param props
 * @returns
 */
export default function StatusText(props: { value: number }) {
  const { value } = props;

  const text: string = useMemo(() => {
    switch (value) {
      case 0:
        return "";
      case 1:
        return "";
      case 2:
        return "";
      case 3:
        return "";
      case 4:
        return "";
      case 5:
        return "*";
      case 6:
        return "";
      case 7:
        return "";
      case 8:
        return "";
      case 9:
        return "";
      case 10:
        return "";
      case 11:
        return "";
      case 12:
        return "";
      default:
        return "";
    }
  }, [value]);

  return (
    <Popover
      content={
        <div>
          <Space vertical>
            <Space>
              <Tag color="light-blue" type="light">
                : 0
              </Tag>
              <Tag color="light-blue" type="light">
                : 1
              </Tag>
              <Tag color="light-blue" type="light">
                : 2
              </Tag>
            </Space>
            <Space>
              <Tag color="light-blue" type="light">
                : 3
              </Tag>
              <Tag color="light-blue" type="light">
                : 4
              </Tag>
              <Tag color="light-blue" type="light">
                *: 5
              </Tag>
            </Space>
            <Space>
              <Tag color="light-blue" type="light">
                : 6
              </Tag>
              <Tag color="light-blue" type="light">
                : 7
              </Tag>
              <Tag color="light-blue" type="light">
                : 8
              </Tag>
            </Space>
            <Space>
              <Tag color="light-blue" type="light">
                : 9
              </Tag>
              <Tag color="light-blue" type="light">
                : 10
              </Tag>
              <Tag color="light-blue" type="light">
                : 11
              </Tag>
            </Space>
            <Space>
              <Tag color="light-blue" type="light">
                : 12
              </Tag>
            </Space>
          </Space>
        </div>
      }
      position="right"
      showArrow
    >
      <Tag color="light-blue" type="light">
        {text}
      </Tag>
    </Popover>
  );
}
